const search = query =>
  fetch(`/api`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ name: query })
  }).then(response => response.json());
// .then(shows => shows.map(show => show.show));

export { search };
